    height: auto;
    padding-top: calc(50% - 4px);
  }
  .HomePage__content .eventsSection .eventName {
    font-size: 64px;
    line-height: 78px;
  }
  .HomePage__content .eventsSection .eventDetails {
    font-size: 24px;
